.myButton {
  border: none;
  background: #3CB6DC;
  color: white;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 5px 15px;
  border-radius: 3px;
  font-weight: 500;
}
.myButton:hover {
  background: #2e7ba6;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.53);
  cursor: pointer;
}
.myButton:active {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.53);
}

