.itemInCart {
  padding: 10px 20px;
  border-bottom: 1px #a1aeb2 solid;
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  position: relative;
}

.title {
  font-size: 17px;
  font-weight: 500;
  font-family: 'Raleway';
}

.price {
  font-size: 16px;
  font-weight: 300;
}

.delete {
  background: url('/src/images/icons/remove.png') no-repeat;
  background-size: contain;
  width: 25px;
  height: 25px;
  position: absolute;
  bottom: 20px;
  right: 10px;
  &:hover {
    cursor: pointer;
  }
}